// GENERAL STYLES (basic, headings etc.)


// BASIC

* {
	font-family: 'Open Sans', sans-serif;
}

.container {
	max-width: 1170px;
}

div:not(h1, h2, h3, h4) {
	font-size: 14px;
	color:var(--text);
}

a {
	color: var(--primary);
	text-decoration: none;
	transition: all .2s;

	&:hover {
		color: var(--secondary);
		text-decoration:none;
	}

	&:focus {
		outline: none;
	}

	&.i_anchor {
		white-space: nowrap;

		&:hover {
				// anchors w/ icons
				color: var(--primary);
				opacity: .85;
			}
	}
}

ul,
ol {
	padding-left: 0;
	list-style: inside;
}

.table {
	color: var(--text);
}

.table-bordered > :not(caption) > * {
	border-color: #ddd;
}


// TEXT & HEADINGS

b,
strong {
	font-weight: 600;
}

.text-default {
	color: var(--text)
}

.text-primary {
	color: var(--primary) !important;
}

.text-warning {
	color: var(--warning) !important;
}

.text-accent {
	color: var(--accent) !important;
}

.text-highlight {
	background: var(--primary_light);
	color: #fff;
	padding: 0 2px;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
	line-height: 1.4;
}

h1,
h2 {
	margin-bottom: 22px;
}

h1,
h2 {
	margin-bottom: 22px;
}


// STRUCTURE, SECTIONS & VISIBILITY

section,
.section {
	padding: 10px;
}

.bg-grey,
.bg-gray {
	background-color: #eee !important;
}

.bg-light {
	background-color: #f7f7f7 !important;
}

.hidden {
	display: none;
}

.relative {
	position: relative;
}