// FORM-CHECK (checkboxes & radios)

.form-check {
	padding-top: 2px;
	padding-bottom: 2px;
	margin-bottom: 0;
	display: block;
	width: 100%;

	.form-check-input {
		border: 0;
		margin-top: 0;
	}

	.form-check-label {
		padding-left: 8px;
		padding-top: 2px;
		display: block;
	}

	&.form-check-inline:not(.input-button-icon):not(.input-button-blank) {
		margin-right: 10px;
		width: initial;
	}
}

.form-check-input:disabled~.form-check-label {
	color:#8d8d8d;
}

// checkbox + radio
input[type="checkbox"],
input[type="radio"] {
	width: 24px;
	height: 24px;
	appearance: none;
}

input[type="checkbox"]:hover,
input[type="radio"]:hover,
.form-check label {
	cursor: pointer;
}

input[type="checkbox"]:focus,
input[type="radio"]:focus {
	border: 0;
	outline: 0;
	box-shadow: none;
	border-color: transparent;
}

// checkbox
input[type="checkbox"] {
	// background-image: skin-specific

	&.required {
		background-position-x: -96px;
	}

	&.required:checked {
		background-position-x: -72px;
	}
}


// SKINS

&.skin-najpoistenie {
	$skin-slug : "skin-najpoistenie";

	input[type="checkbox"] {
		background: url("@/assets/images/#{$skin-slug}/forms/field-radio-check.svg") -48px center no-repeat;

		&:checked {
			background: url("@/assets/images/#{$skin-slug}/forms/field-radio-check.svg") -72px center no-repeat;
		}
	}

	input[type="radio"] {
		background: url("@/assets/images/#{$skin-slug}/forms/field-radio-check.svg") left center no-repeat;

		&:checked {
			background: url("@/assets/images/#{$skin-slug}/forms/field-radio-check.svg") -24px center no-repeat;
		}
	}
}

