// MODAL

.vue_modal {

	// Modal background
	display: block;
	position: fixed;
	z-index: 100;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow: auto;
	background-color: rgba(0, 0, 0, 0.4);
	padding: 10px;

	// Modal Content (Modal)

	&.huge .modal-content {
		margin: 4% auto;
		width: 92%;
		max-width: 1300px;
	}

	.modal-content {
		margin: 15% auto;
		width: auto;
		background-color: #fff !important;
		border-radius: var(--b_radius);
		overflow-wrap: break-word;
	}

	.modal-header,
	.modal-body {
		padding: 20px;
	}

	.modal-header {
		background-color: var(--primary_bg);
		border-radius: var(--b_radius) var(--b_radius) 0 0;

		h4 {
				font-size: 20px;
				margin: 0 auto;
			}

		.i_close {
			width: 14px;
			height: 14px;
			display: inline-block;
			background: url(@/assets/images/common/ic_clear_dark_np.svg) center center no-repeat;
			opacity: 0.4;
			cursor: pointer;
			transition: all 0.2s;

			&:hover,
			&:focus {
				opacity: 1;
			}
		}
	}

	&.huge .modal-header {

		h4 {
			margin: 0;
		}

	}


	//	MEDIA QUERIES

	// Small devices ( 576px)
	@media (min-width: 576px) {

		.modal-content {
			max-width: 500px;
		}

	}
}
