/*

	CUSTOM FIELDS
	fields custom-tailored

	- iButtons - image button fields, incl. blank buttons (image declarations fields-ibuttons.scss)
	- Mixed inputs
		- iButtons + select mix (typ vozidla)
		- iButtons + checkbox (ucel cesty)
		- iButton + text mix (on/off)

*/


// iBUTTONs (incl. blank buttons)

.input-button-icon,
.input-button-blank {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	padding-left: 0;
}

.input-button-icon.h-large input[type="radio"] + label,
.input-button-icon.h-large input[type="checkbox"] + label {
	height: 50px;
}

.input-button-icon.h-large label {
	float: left;
	display: flex;
	justify-content: center;
	flex-direction: column;
}

.input-button-icon input[type="radio"],
.input-button-icon input[type="checkbox"],
.input-button-blank input[type="radio"],
.input-button-blank input[type="checkbox"] {
	display: none;
}

// default state
.input-button-icon input[type="radio"] + label,
.input-button-icon input[type="checkbox"] + label,
.input-button-blank input[type="radio"] + label,
.input-button-blank input[type="checkbox"] + label {
	width: 100%;
	height: 36px;
	padding: 8px;
	border: 1px solid var(--primary);
	border-radius: var(--b_radius);
	text-align: left;
	background: #ffffff;
	color: var(--primary);
	cursor: pointer;
	transition: background 0.2s;
}

.input-button-icon input[type="radio"] + label,
.input-button-icon input[type="checkbox"] + label {
	padding-left: 42px;
}

// :hover
.input-button-icon input[type="radio"]:not(:checked) + label:hover,
.input-button-icon input[type="checkbox"]:not(:checked) + label:hover,
.input-button-blank input[type="radio"]:not(:checked) + label:hover,
.input-button-blank input[type="checkbox"]:not(:checked) + label:hover {
	background-color: var(--primary_bg) !important;
	transition: background 0.2s;
}

// :checked
.input-button-icon input[type="radio"]:checked + label,
.input-button-icon input[type="checkbox"]:checked + label,
.input-button-blank input[type="radio"]:checked + label,
.input-button-blank input[type="checkbox"]:checked + label {
	background: var(--primary);
	border: 1px solid var(--primary);
	color: #fff;
}

// :disabled
.input-button-icon .form-check-input:disabled~.form-check-label,
.input-button-blank .form-check-input:disabled~.form-check-label {
	border: 1px solid #8d8d8d;
	cursor: not-allowed;
}


// MIXED INPUTS

// iButtons + select mix (PZP Typ vozidla)

.input-mixed {
	// same basic roperties as BS5 input-group
	position: relative;
	display: flex;
	flex-wrap: wrap;
	align-items: stretch;
	width: 100%;

	// iButtons + Select (Typ vozidla (PZP))

	.input-button-icon,
	.input-button-blank {
		width: 100%;
		margin-right: 0;
	}

	.input-button-icon input[type="radio"] + label,
	.input-button-icon input[type="checkbox"] + label {
		width: 100%
	}

	select {
		flex: initial;
		flex-grow: 1;
		width: 1%;
		margin-top: 2px;
		margin-bottom: 2px;
		min-width: 96px; // same line as iButtons on Medium screens (768px+)
	}

	// ECV

	&.input-select-text {

		select.form-control {
			flex: 0 1 80px;
			border-radius: var(--b_radius) 0 0 var(--b_radius);
			margin-top: 0;
			margin-bottom: 0;
		}

		input.form-control {
			flex: 1 1 auto;
			width: 1%;
			margin-left: -1px;
			border-radius: 0 var(--b_radius) var(--b_radius) 0;
		}

	}

	// iButtons + checkbox (ucel cesty)

	.form-check:not(.input-button-icon) {
		width: initial;
	}

	// iButton + text fields (Show/Hide (ZP))

	&.input-onoff {
		.input-button-icon,
		.input-button-icon input[type="radio"] + label,
		.input-mixed.input-onoff .input-button-icon input[type="checkbox"] + label {
			width: 100%;
			margin-right: 0;
		}

		.form-control {
			margin: auto 4px auto 0;
			flex: 1 1 auto;
			width: 1%;

			&:last-child {
				margin-right: 0;
			}

		}
	}

}


//	MEDIA QUERIES

// Small devices (576px)
@media (min-width: 576px) {

	.input-button-icon,
	.input-button-blank {
		width: 146px; // min width where the longest text looks ok (Vysokozdvizny)
		margin-right: 4px;
		margin-bottom: 0;
	}


}

// Medium devices (768px)
@media (min-width: 768px) {

	.input-mixed .input-button-icon,
	.input-mixed .input-button-blank {
		width: 146px;
		margin-right: 4px;
	}

	.input-mixed:not(.input-onoff) .input-button-icon input[type="radio"]+label,
	.input-mixed:not(.input-onoff) .input-button-icon input[type="checkbox"]+label {
		width: 146px
	}

	.input-mixed.input-onoff .input-button-icon {
		flex: 0 0 251px;
		margin-right: 4px;
	}

	.input-mixed.input-onoff .input-button-icon input[type="radio"]+label,
	.input-mixed.input-onoff .input-button-icon input[type="checkbox"]+label {
		width: 251px
	}

}


