// INPUT BLOCKS

.input-block {
	background-color: #f7f7f7;
	padding: 8px 25px 8px 5px; // pr 20px ttip + 5px horizontal padding
	margin: 5px 0 5px;
	border: 1px solid #ddd;
	border-radius: var(--b_radius);
	position: relative;
	min-height: initial;

	p {
		margin-bottom: 0;
	}
}

.input-required {
	// background-image: skin-specific
	background-position: 3px 3px;
	background-repeat: no-repeat;
}

.input-label-full .input-area {
	margin-right: 0;
	padding-right: 1em;
}

.input-block-blank {
	background-color: transparent;
	border-color: transparent;
}

.input-block-large {
	min-height: initial;
}

.input-label {
	font-weight: 600;
	width: 100%;
	padding: 4px;
}

.input-area {
	width: 100%;
	padding: 4px;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}


//	MEDIA QUERIES

// Small devices ( 576px)
@media (min-width: 576px) {

	.input-label {
		flex: 0 0 150px;
		-ms-flex: 0 0 150px;
	} /* default (xs) width is 100% */

	.input-required {
		background-repeat: no-repeat;
		background-position: 7px 7px;
	}

	.input-label-full .input-label {
		flex-basis: auto;
	}

	.input-required.input-label-full {
		background-repeat: no-repeat;
		background-position: 3px 3px;
	}

}

// Medium devices (768px)
@media (min-width: 768px) {

	.input-block {
		min-height: 70px; // same height for 1- and 2-line labels
	}

	.input-block-large {
		min-height: 92px;

	}

	.input-heading {
		min-height: 40px;
	}

}
