// COUNTER (PLUS/MINUS)

.input-counter input {
	text-align: center;
}

.input-group-btn {
	display: block;
	width:36px;
	height: 36px;
	cursor: pointer;
}

.input-group-prepend .input-group-btn {
	border-radius: var(--b_radius) 0 0 var(--b_radius);
}

.input-group-append .input-group-btn {
	border-radius: 0 var(--b_radius) var(--b_radius) 0;
}

.btn-minus {
	// background: skin-specific
	background-clip: content-box;
}

.btn-plus {
	// background: skin-specific
	background-clip: content-box;
}


// SKINS

&.skin-najpoistenie {
	$skin-slug : "skin-najpoistenie";

	.btn-minus {
		background: #fff url("@/assets/images/#{$skin-slug}/forms/field-minus.svg") right top no-repeat;
	}

	.btn-plus {
		background: #fff url("@/assets/images/#{$skin-slug}/forms/field-plus.svg") right top no-repeat;
	}
}

