// SELECT

select {
	overflow: hidden;
	// background-image: skin-specific
	background-color: #fff;
	background-repeat: no-repeat;
	background-position: right center;
	cursor: pointer;
}

select.form-control {
	padding-right: 40px; // 36px icon + 4px space
}

select.form-control[multiple] {
	padding-right: 12px;
	background-image: none;
}


// SKINS

&.skin-najpoistenie {
	$skin-slug : "skin-najpoistenie";

	select {
		background-image: url("@/assets/images/#{$skin-slug}/forms/field-select.svg");
	}
}

