// STEP2 (RESULTS) ICONS

&.skin-najpoistenie {

	$skin-slug : "skin-najpoistenie";

	span.i_email {
		background: url("@/assets/images/#{$skin-slug}/results/envelope.svg") center center no-repeat;
	}

	span.i_download {
		background: url("@/assets/images/#{$skin-slug}/results/download-square.svg") center center no-repeat;
	}

	span.i_edit {
		background: url("@/assets/images/#{$skin-slug}/results/edit.svg") center center no-repeat;
	}

	span.i_star {
		background: url("@/assets/images/#{$skin-slug}/results/star.svg") center center no-repeat;
	}

	span.i_star_empty {
		background: url("@/assets/images/#{$skin-slug}/results/star-empty.svg") center center no-repeat;
	}

	span.i_detail {
		background: url("@/assets/images/#{$skin-slug}/results/ic_format_list_bulleted_np.svg") center center no-repeat;
	}

	.incl_yes {
		background: url("@/assets/images/#{$skin-slug}/results/included-yes.svg") left 1px no-repeat;
	}

	.incl_no {
		background: url("@/assets/images/#{$skin-slug}/results/included-no.svg") left 1px no-repeat;
	}

	.incl_special {
		background: url("@/assets/images/#{$skin-slug}/results/included-special.svg") left 1px no-repeat;
	}

	.pr_box_options select {
		background: #fff url("@/assets/images/#{$skin-slug}/results/field-select-s.svg") right center no-repeat;
	}

	.pr_box_name select {
		background: transparent url("@/assets/images/#{$skin-slug}/results/field-select-subtle-s.svg") right center no-repeat;

		&:hover {
			background: transparent url("@/assets/images/#{$skin-slug}/results/field-select-subtle-s-hover.svg") right center no-repeat;
		}
	}

	.pr_box_options .form-check{
		input[type="radio"] {
			background: transparent url("@/assets/images/#{$skin-slug}/results/field-radio-check-s.svg") 0 center no-repeat;

			&:checked {
				background: transparent url("@/assets/images/#{$skin-slug}/results/field-radio-check-s.svg") -15px center no-repeat;
			}
		}

		input[type="checkbox"] {
			background: transparent url("@/assets/images/#{$skin-slug}/results/field-radio-check-s.svg") -30px center no-repeat;

			&:checked {
				background: transparent url("@/assets/images/#{$skin-slug}/results/field-radio-check-s.svg") -45px center no-repeat;
			}
		}
	}

	.pr_box_options .input-datepicker input {
		background: #fff url("@/assets/images/#{$skin-slug}/results/field-calendar-s.svg") right center no-repeat;
	}

}

