// TITLE AREA (STEP2, STEP4 etc.)

.calc_title_area {
	text-align: center;
	margin-bottom: 48px;

	p {
		margin-bottom: 0;
	}

	h2:not(:first-of-type) {

		margin-top: 48px;

	}
}


//	MEDIA QUERIES

// Medium devices (768px)
@media (min-width: 768px) {

	.calc_title_area p {
		padding: 0 20px;
	}

}

// Large devices (992px)
@media (min-width: 992px) {

	.calc_title_area p {
		padding: 0 40px;
	}

}
