&.skin-najpoistenie {

	$skin-slug : "skin-najpoistenie";

	.input-required {
		background-image: url("@/assets/images/#{$skin-slug}/forms/asterisk.svg");
	}

	span.i_download_item {
		background: url("@/assets/images/#{$skin-slug}/general/download.svg") center center no-repeat;
	}

}


