// SEARCH

&.skin-najpoistenie {
	$skin-slug : "skin-najpoistenie";

	.input-search-icon {
		background: url("@/assets/images/#{$skin-slug}/forms/field-search.svg") right center no-repeat;
	}
}


&.skin-finportal {
	$skin-slug : "skin-finportal";

	.input-search-icon {
		background: url("@/assets/images/#{$skin-slug}/forms/field-search.svg") right center no-repeat;
	}
}

&.skin-partners {
	$skin-slug : "skin-partners";

	.input-search-icon {
		background: url("@/assets/images/#{$skin-slug}/forms/field-search.svg") right center no-repeat;
	}
